<template>
   <section class="bg-white text-white">
      <!-- <section class="bg-white text-white"> -->
         <!-- <figure>
         <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->
         <Searcher />

         <article class="bg-blue pb-24">
            <h2 class="text-xl py-6 text-white uppercase
            md:text-6xl
            lg:text-4xl">- {{polekey}} -</h2>
            <div class="container grid md:grid-cols-4 grid-cols-2 m-auto">
               <figure class="w-2/4 cursor-pointer m-auto"
               v-for="(item, index) in icons" :key="index"
               @click="goProduct(item, {
                  country: 'Cuba',
                  product: item.name,
               })">
                  <change-img :item="item"></change-img>
                  <figcaption class="uppercase font-bold text-lg">{{ item.name }}</figcaption>  
               </figure>
            </div>
         </article>
      <!-- </section> -->

      <!-- <section class="bg-foto pb-40">
         <figure>
            <img src="@/assets/img/singlepole-buttom-blue.png" alt="" />            
         </figure> -->
         <!-- SLIDE -->          
         <!-- <h3 class="text-10xl text-white font-Clarissa">Cuba</h3>  

         <button class="btn-booking font-DIN-Bold text-2xl my-5 px-10">decubre</button>
      </section> -->
   </section>
</template>
<script>
import ChangeImg from '@/components/ChangeImg.vue'
import Searcher from "@/components/searcher/Searcher.vue";

export default {
   name: 'SinglePole',
   props: {
      polekey: {
         default: 'cuba'
      }
   },
   components: {
      ChangeImg,
      Searcher
   },
   data() {
      return {
         hover: false,
         icons: [
            {
               img: require('@/assets/img/icons/Icons-01.png'),            
               hover: require('@/assets/img/icons/Icons-01y.png'),    
               name: 'Hoteles',
               keyword: 'hotel',
               active: true
            },            
            {
               img: require('@/assets/img/icons/Icons-02.png'),            
               hover: require('@/assets/img/icons/Icons-02y.png'),            
               name: 'Carros',
               keyword: 'hotel',
               active: false        
            },
            {
               img: require('@/assets/img/icons/Icons-03.png'),            
               hover: require('@/assets/img/icons/Icons-03y.png'),            
               name: 'Traslados',
               keyword: 'hotel',
               active: false        
            },
            {
               img: require('@/assets/img/icons/Icons-04.png'),            
               hover: require('@/assets/img/icons/Icons-04y.png'),
               name: 'Vuelos',
               keyword: 'hotel',
               active: false
            },
            {
               img: require('@/assets/img/icons/Icons-05.png'),            
               hover: require('@/assets/img/icons/Icons-05y.png'),
               name: 'Tramites',
               keyword: 'hotel',
               active: false
            },
            {
               img: require('@/assets/img/icons/Icons-06.png'),            
               hover: require('@/assets/img/icons/Icons-06y.png'),
               name: 'Viveres',
               keyword: 'hotel',
               active: false
            },
            {
               img: require('@/assets/img/icons/Icons-07.png'),            
               hover: require('@/assets/img/icons/Icons-07y.png'),
               name: 'Paqueteria',
               keyword: 'hotel',
               active: false
            },
            {
               img: require('@/assets/img/icons/Icons-08.png'),            
               hover: require('@/assets/img/icons/Icons-08y.png'),
               name: 'Remesas',
               keyword: 'hotel',
               active: false
            },
         ]
      }
   },   
   methods: {
      goProduct(obj, params) {
         if (obj.active) {
            this.$router.push({
               name: 'Products',
               params: {poleKey: this.polekey, productKey: obj.keyword}
            })
         } else {
            this.sendRequest(params)
         }
      },
      toggleImg() {
         console.log(this)
         
         // obj.setAttribute('src', img)
      },
      sendRequest(params) {
         localStorage.removeItem('request_info')
         const request = {
         country: params.country,         
         product: params.product,         
         is_home_request: true        
         }            
         localStorage.setItem('request_info', JSON.stringify(request))
         this.$router.push({ name: "SendRequest" });
      }
   }
}
</script>
