<template>
    <div class="bg-before">
        <img :src="toogleImg" @mouseover="hover=!hover" @mouseleave="hover=false" alt="Icon">        
    </div>
</template>
<script>
export default {
    name: 'ChangeImage',
    props: {
        item: {}
    },
    data() {
        return {
            hover: false,
            img: this.item.img,
            active: this.item.active
        }
    },
    computed: {
        toogleImg() {
            return (this.hover) ? this.item.hover : this.item.img
        }
    }
}
</script>
